@use '../../Core/Color';
@use '../../Core/Shape';
@use '../../Core/Typography';

@use '../../Core/Feature';

@use 'sass:map';
@use 'sc-button-base' as base;
@use 'sc-button-theme' as theme;

@mixin render(
    $color-theme: (),
    $shape-theme: (),
    $typography-theme: (),
    $type: (
        'default'
    )
) {

    $_scope-exceptions: ();

    $_color-theme: map.merge(theme.$init-color-theme, $color-theme);
    $_shape-theme: map.merge(theme.$init-shape-theme, $shape-theme);
    $_typography-theme: map.merge(theme.$init-typography-theme, $typography-theme);

    // Base
    @include base.base() {

        .sc-button__ripple {
            position: absolute;
            border-radius: 50%;
            transform: scale(0);
            animation: ripple 600ms linear;
            background-color: rgba(Color.get-color_($_color-theme, 'fill', 'ripple'), 0.25);
        }

        @keyframes ripple {
            to {
                transform: scale(4);
                opacity: 0;
            }
        }

        @if Feature.has-item($type, default) or Feature.has-item($type, text) {
            @include Color.apply('text', $_color-theme, $_scope-exceptions);
            @include Typography.apply($_typography-theme);
            @include Shape.apply($_shape-theme);
        }

        @if Feature.has-item($type, outlined) {
            &.sc-button--outlined {
                @include Color.apply('outlined', $_color-theme, $_scope-exceptions);
                @include Typography.apply($_typography-theme);
                @include Shape.apply($_shape-theme);
            }
        }

        @if Feature.has-item($type, filled) {
            &.sc-button--filled {
                @include Color.apply('filled', $_color-theme, $_scope-exceptions);
                @include Typography.apply($_typography-theme);
                @include Shape.apply($_shape-theme);
            }
        }

        @content
    }
}
